.Airspeed {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 105px;
}

.AirspeedOverlay {
  background: black;
  opacity: 0.1;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 5px;
}

.AirspeedTape {
  background: #67b55d;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 100px;
  right: 0px;
}
